import { Component, Input } from '@angular/core';
import { BusinessFunction } from '../model/types';
import { TranslationsService } from '@backbase/accessgroup-common-ang';

@Component({
  selector: 'bb-business-function-name',
  templateUrl: './business-function-name.component.html',
})
export class BusinessFunctionNameComponent {
  /**
   * Business function for which the name is shown
   */
  @Input() businessFunction: BusinessFunction | undefined;

  constructor(private translationsService: TranslationsService) {}

  get name(): string | undefined {
    if (!this.businessFunction) return '';

    return this.translationsService.getBusinessFunctionName(this.businessFunction.functionCode) || this.businessFunction.name;
  }
}
